<template>
    <el-container style="margin-bottom: 60px;margin-top: 20px;margin-left: 110px;">
        <el-header height="200px;">
            <el-row>
                <!--榜单图片-->
                <img :src="oneTopList.coverImgUrl" style="width: 200px;height: 200px;"/>
                <!--右边歌曲数量以及播放量部分-->
                <div style="float:right;color: dimgray;opacity: 0.7;font-size: 17px;margin-right: 170px;">
                    <p style="text-align: center;">歌曲数 | 播放量</p>
                    <p style="float:right;margin: 0">
						{{oneTopList.trackCount}}首 |{{oneTopList.playCount}}次
					</p>
                </div>
				<!--榜单名字-->
                <span style="position: absolute;top: 0px;left: 240px;font-weight: 400;font-size: 30px;">{{oneTopList.name}}</span>
                <!--收藏次数-->    
                <el-button size="medium" style="position: absolute;top: 75px;left: 240px;color: black">
                    收藏次数({{oneTopList.subscribedCount}})
                </el-button>
                <!--榜单介绍-->
                <span style="position: absolute;top: 140px;left: 240px;">
                    <span style="color: rgb(12,115,194);cursor: pointer;font-size: 18px;">简介: </span>                 
                </span>
                <!--简介的具体信息-->
                <el-collapse style="position: absolute;top: 165px;left: 240px;border: none;z-index: 999;">
                    <el-collapse-item :title="oneTopList.description + '...' ">
                        <p>{{oneTopList.description}}</p>
                    </el-collapse-item>
                </el-collapse>
            </el-row>
        </el-header>
		<!--歌曲列表和评论部分-->
        <el-main style="margin-top: 3px;">
            <el-menu class="el-menu-demo" mode="horizontal">
                <el-menu-item index="1" @click="getMusicList">歌曲列表</el-menu-item>
				<el-menu-item index="2" @click="getComment">评论({{oneTopList.commentCount}})</el-menu-item>
            </el-menu>
			<!-- 显示子路由 -->
            <router-view></router-view>
        </el-main>
    </el-container>
</template>

<script>
	export default {
		data() {
		    return {
		        //存放获取到的对应id的榜单信息
		        oneTopList: {},
		    }
		},
		created(){
			//获取所点击榜单(id)所对应的榜单数据
			this.getTopListById()
		},
		methods:{
			//获得所有榜单的数据
			getTopListById() {
				//根据榜单页面传来的id查询该榜单下的数据信息
				var id=this.$store.state.id;
				var that=this;
				//get请求
				this.$axios.get("https://autumnfish.cn/playlist/detail?id="+id)
				.then((res)=>{
					//回调函数中，this的指向不是vue实例，而是当前的回调函数对象
					that.oneTopList = res.data.playlist;
					//将获取到的trackIds存入store中
					this.$store.commit("addTrackIds",res.data.playlist.trackIds);
				},(err)=>{})
			},
			//跳转到榜单歌曲信息页面
			getMusicList(){
				this.$router.push({
					//window.location.href=""
					name:"musicList",
				});
			},
			//跳转到榜单评论信息页面
			getComment(){
				this.$router.push({
					//window.location.href=""
					name:"comment",
				});
			},
		}
	}
</script>

<style scoped>
.el-menu.el-menu--horizontal {
    border-bottom: solid 1px #e6e6e6;
    width: 1000px;
}
</style>
